export default {
	data() {
		return {
			img: '',
			height: '',
			show: false,
			animationData: {},
			chooseclasses: '',
			bigiconbaseUrl: '', //大图本地路径
			smallIconbaseUrl: '', //小图路径
			XS: '', //屏幕系数比
			IConBili: '', //大图比例
			cavheight: '', //画布高度
			context: null,
			imgHeight: '',
			winWidth: '',
			winHeight: '',
			shareImage: '',
			canvasHeight: ''
		}
	},
	methods: {
		share(scene) {
			uni.showLoading({
				title: '微信唤起中...'
			})
			uni.share({
				provider: "weixin",
				scene: scene,
				type: 2,
				imageUrl: this.shareImage,
				success: function(res) {
					uni.hideLoading()
				},
				fail: function(err) {
					uni.hideLoading()
				}
			});
		},
		// 下载图片
		dowloadbigicon(bigurl) {
			uni.downloadFile({
				url: bigurl, //仅为示例，并非真实的资源
				success: (res) => {
					if (res.statusCode === 200) {
						this.bigiconbaseUrl = res.tempFilePath;
						this.drawBigimage();
					}
				}
			});
		},
		drawBigimage() {
			this.context.beginPath()
			if (this.bigiconbaseUrl) {
				this.context.drawImage(this.bigiconbaseUrl, 0, 0, this.winWidth, this.winHeight);
			}
			this.context.draw(false, () => {
				uni.canvasToTempFilePath({
					width: this.winWidth,
					height: this.winHeight,
					quality: 1,
					fileType: 'jpg',
					canvasId: 'canvas',
					success: res => {
						this.shareImage = res.tempFilePath;
					}
				})
			});
		},
	},
	onLoad(e) {
		this.$store.dispatch('getShareCode', {
			data_id: e.id
		}).then(res => {
			this.img = res.data
			uni.getSystemInfo({
				success: (res) => {
					this.height = res.screenHeight + 'px'
					this.winWidth = res.windowWidth
					this.winHeight = res.windowHeight
					this.dowloadbigicon(this.img)
				}
			})
		}).catch(err => {})
	},
	onShow() {
		uni.hideLoading()
		this.context = uni.createCanvasContext('canvas');
	},
	// 监听页面加载
	onReady: function() {
		this.context = uni.createCanvasContext('canvas');
	},
	onNavigationBarButtonTap() {
		this.show = !this.show
		var animation = uni.createAnimation({
			duration: 300,
			timingFunction: 'ease',
		})
		this.animation = animation
		setTimeout(() => {
			animation.translateY(-100).step()
			this.animationData = animation.export()
		}, 0)
	}
}
